body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-smi: 13px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-xs: 12px;
  --font-size-4xl: 23px;
  --font-size-13xl: 32px;
  --font-size-17xl: 36px;
  --font-size-base: 16px;
  --font-size-mini: 15px;
  --font-size-3xl: 22px;
  --font-size-lg: 18px;

  /* Colors */
  --color-whitesmoke: #efefef;
  --color-white: #fff;
  --color-mediumseagreen: #0acf97;
  --color-aliceblue: #e3eaef;
  --color-darkslategray: #3f3f3f;
  --color-indianred: #f1556c;
  --color-coral: #e56b41;
  --color-black: #000;
  --color-silver-100: #c4c4c4;
  --color-silver-200: #c1c1c1;
  --color-silver-300: #b7b7b7;
  --color-gray: #fcfcfc;
  --color-darkgray-100: #9facb2;
  --color-darkgray-200: #98a6ad;
  --color-darkturquoise: #02c0ce;
  --color-darkturquoise-100: rgba(2, 192, 206, 0);
  --color-royalblue: #2d7bf4;
  --color-gainsboro-100: #ddd;
  --color-gainsboro-200: rgba(217, 217, 217, 0);
  --icon-color: #6c757d;

  /* Gaps */
  --gap-11xs: 2px;
  --gap-307xl: 326px;
  --gap-298xl: 317px;
  --gap-10xs: 3px;
  --gap-base: 16px;
  --gap-2xs: 11px;
  --gap-xs: 12px;
  --gap-smi: 13px;
  --gap-lg: 18px;
  --gap-mini: 15px;

  /* Paddings */
  --padding-7xs: 6px;
  --padding-mini: 15px;
  --padding-5xl: 24px;

  /* border radiuses */
  --br-8xs: 5px;
  --br-31xl: 50px;
  --br-4xs: 9px;
  --br-7xs: 6px;
  --br-9xs: 4px;
  --br-11xs: 2px;
  --br-lgi: 19px;

  /* Effects */
  --bubbles: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 2px 5px rgba(0, 0, 0, 0.05);
}
